import React , {setState} from 'react'
import "./Card.css"

function Card({name , amount , uid ,status,user ,url ,message}) {

const toShow  =status ==1
    
    return (
        <div className = "card">
    {toShow?<p className = "mcontent">
       <h3>   <img src = {url} width  = "40px" height = "40px"></img> {name} has donated</h3>
       <h3>  the amount of  Rs.{amount}</h3>
       <h3>"{message}"</h3>
     
       </p> : <div>{name} 's donation have not been verified yet</div>}
        
         </div>
    )
}

export default Card ;

